.profile-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content:space-evenly;
    min-height: 100vh;
    padding-top: 80px;
    background-color: rgb(246, 240, 196);
  }
  .profile-head {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(40, 146, 238);
  }
  .about-profile{
      width: 50%;
  }
 
  .profile-pic {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 0 0 rgb(26, 114, 230);
    border-radius: 50%;
    height: 320px;
    width: 320px;
    margin: 0 0 0 100px;
  }
  
  .profile-pic-bg img{
      border-radius: 50%;
      background-size: cover;
      background-position: center;
      transform: rotateZ(-9deg);
  }
  .profile-pic-bg:hover {
    transform: scale(1.08);
    transition: 1s ease-out;
  }
  
  @media screen and (max-width: 1110px) {
    .profile-pic {
      margin: 0 0 0 80px;
    }
    
    .profile-pic-bg img{
      transform: rotateZ(-9deg);
    }
  }
  @media screen and (max-width: 910px) {
    .profile-pic {
      height: 320px;
      width: 320px;
      margin: 0 0 0 4px;
    }
  }
  
  @media screen and (max-width: 810px) {
    .profile-pic {
      margin: 0 !important;
      padding-top: 20px;
    }
    .profile-head {
      flex-direction: column;
      margin: 40px 0 0 0;
    }
    .about-profile{
        margin: 25px 0 0;
        width: 60%;
      }
  }
  @media screen and (max-width: 375px) {
    .profile-pic {
      height: 275px;
      width: 275px;
      margin-top: -90px;
      padding-top: 10px;
    }
    .about-profile {
        width: 90%;
      }
  }
  