html {
    box-sizing: border-box;
    font-size: 50%;
}

.experience{
    background-color: #eee;
    font-family: 'poppins', sans-serif;
    padding-top: 70px;
    display: block;
    min-height: 100vh;
}

.card{
    background-color: rgb(120, 214, 240);
    border-radius: 1rem;
    display:inline-block;
    box-shadow: 0 30rem 6rem rgba(0 0 0 0.1);
    cursor: pointer;
    transition: 0.2s;
    width: 23vw;
    height: 37vh;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-top: 6vh;
}
.card:hover{
    transform: translateY(-2%);
    box-shadow: 0 4rem 8rem rgba(0 0 0 0.5);
}
.card-image{
    display: block;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    width: 100%;
    height: 25rem;
    object-fit: cover;
}

.card-headr{
    font-size: 3rem;
    font-weight: 500;
    color: #0d0d0d;
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-txt{
    font-size: 1.6rem;
    letter-spacing: 0.1rem;
    line-height: 1.7;
    color: #3d3d3d;
    /* margin-bottom: 2.5rem; */
    display: flex;
        justify-content: center;
        align-items: center;
}
@media screen and (max-width:1178px){
    html{
        font-size: 45%;
    }
}

@media screen and (max-width:1096px) {
    .card{
        width: 28vw;
        margin-left: 10vw;
        margin-right: 10vw;
        height: 43vh;
    }
    .card-image{
        display: block;
        width: 100%;
        height: 26rem;
        object-fit: cover;
    }
    .card-headr{
        display: flex;
        justify-content: center;
    }
    .card-txt{
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width:888px){
    html{
        font-size: 42%;
    }
    
    .card-image{
        display: block;
        width: 100%;
        height: 32rem;
        object-fit: cover;
    }
}
@media screen and (max-width:810px){
    html{
        font-size: 38%;
    }
}
@media screen and (max-width:796px){
    
    .card-image{
        display: block;
        width: 100%;
        height: 35rem;
        object-fit: cover;
    }
}
@media screen and (max-width:745px){
    html{
        font-size: 35%;
    }
    .card-cont{
        padding-top: 4vh;
    }
    
}
@media screen and (max-width:673px){
    .card{
        width: 50vw;
        margin-left: 25vw;
        margin-right: 10vw;
        height: 45vh;
    }
    .card-image{
        display: block;
        width: 100%;
        height: 42rem;
        object-fit: cover;
    }
    
}
@media screen and (max-width:480px){
    html{
        font-size: 35%;
    }
    .card{
        width: 70vw;
        margin-left: 15vw;
        margin-right: 10vw;
        height: auto;
    }
    
    .card-image{
        display: block;
        width: 100%;
        height: 42rem;
        object-fit: cover;
    }
    
    .card-headr{
        display: flex;
        justify-content: center;
        margin-top: 3vh;
    }
    .card-txt{
        display: flex;
        justify-content: center;
    }
}