*{
    margin: 0;
    padding: 0;
}
html {
    box-sizing: border-box;
    font-size: 50%;
}

.project{
    background-color: #eee;
    font-family: 'poppins', sans-serif;
    display: block;
    min-height: 100vh;
    padding-top: 50px;
}

.cards{
    background-color: #fff;
    border-radius: 0.4rem;
    display:inline-block;
    box-shadow: 0 30rem 6rem rgba(0 0 0 0.1);
    cursor: pointer;
    transition: 0.2s;
    width: 23vw;
    height: 35vh;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-top: 6vh;
}
.cards:hover{
    transform: translateY(-2%);
    box-shadow: 0 4rem 8rem rgba(0 0 0 0.5);
}
.card-img{
    display: block;
    width: 100%;
    height: 20rem;
    object-fit: cover;
}

.card-header{
    font-size: 3rem;
    font-weight: 500;
    color: #0d0d0d;
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-text{
    font-size: 1.6rem;
    letter-spacing: 0.1rem;
    line-height: 1.7;
    color: #3d3d3d;
    margin-bottom: 2.5rem;
    display: flex;
        justify-content: center;
        align-items: center;
}
.card-btn{
    display: block;
    width: 100%;
    padding: 1.5rem;
    font-size: 2rem;
    text-align: center;
    color: #3363ff;
    background-color: #d8e0fd;
    border: none;
    border-radius: 0.4rem;
    transition: 0.2s;
    cursor: pointer;
    letter-spacing: 0.1rem;
   margin-bottom: 0;
}
.card-btn:hover, .card-btn:active{
    background-color: #c2cffc;
}
@media screen and (max-width:1096px) {
    html{
        font-size: 50%
    }
    .cards{
        width: 28vw;
        margin-left: 10vw;
        margin-right: 10vw;
        height: 43vh;
    }
    .card-img{
        display: block;
        width: 100%;
        height: 26rem;
        object-fit: cover;
    }
    .card-header{
        display: flex;
        justify-content: center;
    }
    .card-text{
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width:888px){
    html{
        font-size: 45%;
    }
    
    .card-img{
        display: block;
        width: 100%;
        height: 32rem;
        object-fit: cover;
    }
}
@media screen and (max-width:796px){
    html{
        font-size: 42%;
    }
    
    .card-img{
        display: block;
        width: 100%;
        height: 35rem;
        object-fit: cover;
    }
}
@media screen and (max-width:745px){
    html{
        font-size: 38%;
    }
    .cards-content{
        padding-top: 4vh;
    }
    .card-btn{
        margin-top: 5.5vh;
    }
}
@media screen and (max-width:673px){
    .cards{
        width: 50vw;
        margin-left: 25vw;
        margin-right: 10vw;
        height: 45vh;
    }
    .card-img{
        display: block;
        width: 100%;
        height: 42rem;
        object-fit: cover;
    }
    .card-btn{
        margin-top: 1vh;
    }
}
@media screen and (max-width:480px){
    html{
        font-size: 35%;
    }
    .cards{
        width: 70vw;
        margin-left: 15vw;
        margin-right: 10vw;
        height: auto;
    }
    
    .card-img{
        display: block;
        width: 100%;
        height: 42rem;
        object-fit: cover;
    }
    .card-btn{
        margin-top: 3.25vh;
    }
    .card-header{
        display: flex;
        justify-content: center;
        margin-top: 3vh;
    }
    .card-text{
        display: flex;
        justify-content: center;
    }
}