@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,700;1,100;1,200;1,300&family=Roboto:wght@300;400&display=swap");
.header{
  width: 100%;
  height: 60px;
  color: white;
  top: 0;
  left: 0;
  padding: 1rem;
  justify-content: space-between;
  display: flex;
  position: fixed;
  margin-top: 0;
  align-items: center;
  background: #111;
  z-index: 100;
}
.header .fa-bars{
  display: none;
  margin-right: 40px;
}
header h1 {
  font-size: 2.8rem;
  padding-left: 20px;
  padding-top: 10px;
  cursor: pointer;
}
.header .navbar ul {
  display: flex;
  list-style-type: none;
  align-items: center;
  justify-content: space-around;
  margin-right: 30px;
}

.header .navbar ul li {
  margin: 0rem 1.5rem;
  font-size: 1.5rem;
  color: #fff;
  cursor: pointer;
}
.header .navbar ul li .link{
  color: #fff;
  font-size: 3rem;
  text-decoration: none;
}
.header .navbar ul li .link:hover {
  color: tomato;
  transform: scale(1.05);
} 
.profile-container {
  display: flex;
  align-items: center;
  text-align: center;
  padding-top: 80px;
  justify-content: center;
  height: 10%;
  min-height: fit-content;
  background-color: rgb(47, 56, 70);
}
.profile-parent {
  display: flex;
  align-items: center;
  color: aliceblue;
}
.profile-details-name {
  font-size: 25px;
  font-family: "poppins";
  padding-top: 10px;
}
.profile-details-role h1 {
  font-size: 40px;
  font-family: cursive;
}
.primary-text {
  color: #fff;
}
.highlighted-text {
  color: rgb(247, 90, 33);
}
.profile-role-tagline {
  font-size: 25px;
  margin: 5px 0 0 0;
  font-family: "poppins Light";
}
.profile-options{
  padding-top: 30px;
}
.profile-options .highlighted-btn {
  margin: 0 0 0 28px;
}
.profile-options .highlighted-btn:hover {
  margin: 0 0 0 28px;
  background-color: cornsilk;
  color: #111;
  transform: scale(1.05);
}
.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 0 0 #fff;
  border-radius: 50%;
  height: 400px;
  width: 400px;
  margin: 0 0 0 100px;
}
.profile-picture-background {
  height: 90%;
  width: 90%;
  border-radius: 50%;
  background-image: url(../../assets/Home/pic.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transform: rotateZ(-9deg);
}
.profile-picture-background:hover {
  transform: scale(1.08) rotateZ(-9deg);
  transition: 1s ease-out;
}
.colz .colz-icon a {
  padding: 8px;
  padding-left: 0;
  background-size: cover;
}
.colz .colz-icon i:hover {
  transform: scale(1.2);
}

@media screen and (max-width: 1110px) {
  .header .navbar ul li .link{
    font-size: 3rem;
  }
  .profile-picture {
    height: 350px;
    width: 350px;
    margin: 0 0 0 80px;
  }
  .profile-details-name {
    font-size: 20px;
  }
  .profile-options .btn {
    width: 120px;
    padding: 11px 0;
    z-index: 700;
  }
  .profile-picture-background {
    transform: rotateZ(-9deg);
  }
}
@media screen and (max-width: 910px) {
  .profile-picture {
    height: 320px;
    width: 320px;
    margin: 0 0 0 4px;
  }
}
@media screen and (max-width: 850px) {
  .header .fa-bars {
    display: block;
    cursor: pointer;
  }
  .header .navbar {
    position: fixed;
    height: 91vh;
    top: 9vh;
    left: -120vw;
    background: #111;
    transition: .8s;
  }
  .header .navbar ul {
    height: 90%;
    width: 100vw;
    padding-left: 10px;
    display: flex;
    flex-flow: column;
  }
  .header .navbar ul li {
    font-size: 2rem;
    margin: 1rem 0;
    text-decoration: none;
  }
  
  .header .navbar ul li .link{
    color: #fff;
    text-decoration: none;
  }
  
}
@media screen and (max-width: 810px) {
  .header .navbar ul li .link{
    font-size: 4rem;
  }
  .profile-picture {
    margin: 0 !important;
    padding-top: 20px;
  }
  .profile-parent {
    flex-direction: column-reverse;
    margin: 40px 0 0 0;
  }
  .profile-options {
    display: flex;
    justify-content: space-around;
  }
  .profile-details {
    margin: 25px 0 0;
  }
  .profile-options .highlighted-btn {
    margin: 0 !important;
  }
  .profile-options .profile-details-role {
    text-align: center;
  }
}
@media screen and (max-width: 375px) {
  .header .navbar ul li .link{
    font-size: 4rem;
  }
  .profile-picture {
    height: 275px;
    width: 275px;
    margin-top: -90px;
    padding-top: 10px;
  }
  .profile-details {
    width: 90%;
  }
}
